"use strict";
//setup
import { OpenAI } from "openai";

//functions

//generic call to API
export async function call(messages, apiKey) {
  const openai = new OpenAI({
    apiKey,
    dangerouslyAllowBrowser: true,
  });

  const completion = await openai.chat.completions.create({
    model: "gpt-4o-mini",
    messages,
  });

  return completion.choices[0].message.content;
}

//generates a prompt sentence
export async function getSentence(query, apiKey) {
  const messages = [
    {
      role: "user",
      content:
        "Give me a plain text JSON with a simple  English sentence featuring this word. Also tell me the target language with this code. Obey all notes.",
    },
    {
      role: "user",
      content:
        '{"languageCode": "zh", "word": "fruit", "difficulty": "beginner", "notes": "ask a question"}',
    },
    {
      role: "assistant",
      content:
        '{"targetLanguage": "Chinese", "en": "What is your favorite fruit to eat?"}',
    },
    {
      role: "user",
      content: JSON.stringify(query),
    },
  ];
  return await call(messages, apiKey);
}

//generates feedback
export async function getCorrections(query, apiKey) {
  const messages = [
    {
      role: "user",
      content:
        "Give a JSON with my translation attempt and corrections on my attempt. If my attempt is perfect, leave the corrections array empty. Include transliterations if applicable.",
    },
    {
      role: "user",
      content:
        '{"targetLanguage": "zh", "originalSentence": "This dog has brown fur.", "attempt": ""}',
    },
    {
      role: "assistant",
      content: `{"attempt": " (zhge gu yu zngs mof)", "correctedAttempt": "(zhzh gu yu zngsde mof.)", "corrections": ["Use \\" (zh)\\" instead of \\" (ge)\\" because \\"\\" is the measure word for animals.", "Insert \\" (de)\\" into \\" (zngsde mof)\\" to connect the adjective to the noun.]}`,
    },
    { role: "user", content: JSON.stringify(query) },
  ];
  return await call(messages, apiKey);
}
